import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Rigister from '../views/Rigister.vue'
import Show  from "../views/Show";
import Login from  '../views/Login'
import Content from "../views/Content";
import Insert from "../views/Insert";
import Detail from '../views/Detail';
import Update from '../views/update';
Vue.use(VueRouter)
  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
    {
      path:'/rigister',
      name:'Rigister',
      component:Rigister
    },
    {
      path:'/login',
      name:'Login',
      component:Login
    },
    {
      path:'/show',
      name:'Show',
      component:Show,
      redirect:'/content',
      children:[
        {
      path:'/content',
          name:'Content',
          component:Content,
        },
        {
          path:'/detail',
          name:'Detail',
          component:Detail
        },
        {
          path:'/insert',
          name:'Insert',
          component:Insert
        },
        {
          path:'/update',
          name:'Update',
          component:Update
        },
      ]
    }
]

const router = new VueRouter({
  routes
})

export default router
