<template>

    <div id="app" >
        <el-container>

            <el-form  :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm login-box"  style="height: 2000px; width: 2000px">
                <el-form-item label="新闻标题" prop="title" >
                    <el-input type="text" v-model="ruleForm.title" size="medium" ></el-input>
                </el-form-item>
                <el-form-item label="内容" prop="content">
                    <el-input type="text" v-model="ruleForm.content" autocomplete="off" ></el-input>
                </el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>

            </el-form>
        </el-container>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                ruleForm: {
                    title: '',
                    content: '',
                },
                rules: {
                    title: [
                        { required: true, message: '请输入标题', trigger: 'blur' },
                    ],
                    content: [
                        { required: true, message: '请输入内容', trigger: 'blur' },

                    ],}
                };
        },
        methods: {
            submitForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let {title,content}=this.ruleForm;
                        console.log(title,content);
                        this.$store.commit('add', {
                            title,content
                        });
                        this.$router.push('/content');
                        this.$message({
                            message: '恭喜你，成功添加一条数据！',
                            type: 'success'
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            open2() {
                this.$message({
                    message: '恭喜你，这是一条成功消息',
                    type: 'success'
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>
<style lang="scss" scoped>
    .login-box {
        margin: 0 auto;
    }
</style>

