<template >
    <div>
        <div>
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="新闻标题">
                    <el-input v-model="formInline.title" ></el-input>
                </el-form-item>
                <el-form-item label="新闻摘要">
                    <el-input v-model="formInline.content" ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </el-form>

            <el-table
                    :data="tableData"

                    border
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="序号"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="title"
                        label="标题"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="content"
                        label="摘要">
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="300">
                    <template  slot-scope="scope">
                        <el-button type="text" size="small" @click="getDetail(scope.row)">
                            详情
                        </el-button>

                        <el-button  v-show="isAdmin"  type="text" size="small" @click="update(scope.row)">更新</el-button>

                        <el-button  v-show="isAdmin" type="text" size="small"  @click="mydelete(scope.row)">删除</el-button>
                    </template>

                </el-table-column>
            </el-table>
        </div>
        <router-view></router-view>
    </div>

</template>

<script>
    export default {
        data() {
            return {
                formInline: {
                    title: '',
                    content: ''
                },
                // isAdmin:false,
                scope:null,
                tableData: this.$store.state.tableData
            }
        },
        beforeCreate(){
            this.isAdmin=this.$store.state.isAdmin;
        },
       methods: {
                onSubmit(){
                    let {title,content}=this.formInline;
                    console.log('content',content);
                    this.$store.commit('search',{title,content});
                    this.tableData=this.$store.state.searchData;
                },
                mydelete(row){
               console.log(row.id);
               let {id}=row;
               // console.log(this.record);
               this.$store.commit('mydelete', id)
                },
           update(row) {
                    let {id,content,title}=row;
               this.$router.push({
                   path:'/update',
                   query: {
                       content,
                       id,
                       title

                   },
               });
           },


           getDetail(row)
           {
               this.$store.commit('detail', row);
               this.$router.push("/detail");
           }


        }
    }
</script>
<style scoped>

</style>